.card__title {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  color: #000;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  max-width: calc(100% - 22px);
  margin: 25px 0 31px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
